import React from 'react';
import './style.css';

interface Props {
  type?: string
  onClick: React.MouseEventHandler
}

function addSvg() {
  return ( 
    <svg width="42px" height="42px" viewBox="0 0 42 42" version="1.1" xmlns="http://www.w3.org/2000/svg">
        <g id="Page-1" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
            <g id="Version1" transform="translate(-1034.000000, -1645.000000)" stroke="#202124">
                <g id="Group-4" transform="translate(330.000000, 1280.000000)">
                    <g id="Group-2" transform="translate(705.000000, 366.000000)">
                        <circle id="Oval-Copy" fill="#FFFFFF" cx="20" cy="20" r="20"></circle>
                        <line x1="9.33333333" y1="20" x2="30.6666667" y2="20" id="Line-2-Copy" strokeLinecap="square"></line>
                        <line x1="9.33333333" y1="20" x2="30.6666667" y2="20" id="Line-2-Copy-2" strokeLinecap="square" transform="translate(20.000000, 20.000000) rotate(90.000000) translate(-20.000000, -20.000000) "></line>
                    </g>
                </g>
            </g>
        </g>
    </svg>
  );
}

function subSvg() {
  return (
    <svg width="42px" height="42px" viewBox="0 0 42 42" version="1.1" xmlns="http://www.w3.org/2000/svg">
        <g id="Page-1" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
            <g id="Version1" transform="translate(-828.000000, -1645.000000)" stroke="#404143">
                <g id="Group-4" transform="translate(330.000000, 1280.000000)">
                    <g id="Group-3" transform="translate(499.000000, 366.000000)">
                        <circle id="Oval" fill="#FFFFFF" cx="20" cy="20" r="20"></circle>
                        <line x1="9.33333333" y1="20" x2="30.6666667" y2="20" id="Line-2" strokeLinecap="square"></line>
                    </g>
                </g>
            </g>
        </g>
    </svg>
  );
}

export default function AddSub(props: React.PropsWithChildren<Props>) {
  const { type, onClick } = props;
  return (
    <div className="AddSub" onClick={onClick}>{type === 'sub' ? subSvg() : addSvg()}</div>
  );
}