import React from 'react';
import { Table } from 'react-bootstrap';

const tiers = [
'Tier 1|0-999|0.2',
'Tier 2|1000-1999|0.22',
'Tier 3|2000-2999|0.242',
'Tier 4|3000-3999|0.2662',
'Tier 5|4000-4999|0.29282',
'Tier 6|5000-5999|0.322102',
'Tier 7|6000-6999|0.3543122',
'Tier 8|7000-7999|0.38974342',
'Tier 9|8000-8999|0.428717762',
'Tier 10|9000-9999|0.4715895382'
];


export default function StepPriceTable() {
  return (
    <Table striped bordered hover>
      <thead>
        <tr>
          <th>Tier</th>
          <th>Token ID</th>
          <th>Price</th>
        </tr>
      </thead>
      <tbody>
        {tiers.map((tier, i) => {
          const fields = tier.split('|');
          return (
            <tr key={i}>
              {fields.map((field, j) => <td key={j}>{field}</td>)}
            </tr>
          );
        })}
      </tbody>
    </Table>
  );
}