import React from 'react';
import { Table } from 'react-bootstrap';

const tiers = [
  'Tier 1|0-999|0.90%|0.40%|0.00%',
  'Tier 2|1000-1999|0.90%|0.40%|0.00%',
  'Tier 3|2000-2999|1.00%|0.50%|0.10%',
  'Tier 4|3000-3999|1.10%|0.50%|0.10%',
  'Tier 5|4000-4999|1.20%|0.60%|0.10%',
  'Tier 6|5000-5999|1.30%|0.60%|0.10%',
  'Tier 7|6000-6999|1.40%|0.70%|0.10%',
  'Tier 8|7000-7999|1.50%|0.70%|0.10%',
  'Tier 9|8000-8999|1.70%|0.70%|0.10%',
  'Tier 10|9000-9999|2.00%|0.90%|0.30%',
];


export default function RarityTable() {
  return (
    <Table striped bordered hover>
      <thead>
        <tr>
          <th>Tier</th>
          <th>Token ID</th>
          <th>Rare</th>
          <th>Super Rare</th>
          <th>Extremely Rare</th>
        </tr>
      </thead>
      <tbody>
        {tiers.map((tier, i) => {
          const fields = tier.split('|');
          return (
            <tr key={i}>
              {fields.map((field, j) => <td key={j}>{field}</td>)}
            </tr>
          );
        })}
      </tbody>
    </Table>
  );
}