import React from 'react';
import { Row, Col, Button, Container } from 'react-bootstrap';
import ETH from '../ETH';
import AddSub from '../AddSub';
import { formatEther, formatAddress } from '../utils';
import './style.css';
import AutosizeInput from 'react-input-autosize';


interface Props {
  onSub: React.MouseEventHandler
  onAdd: React.MouseEventHandler
  onBuy: React.MouseEventHandler
  onInput: React.ChangeEventHandler
  onInputValue: React.ChangeEventHandler
  onFocusValue: React.ChangeEventHandler
  qty: number
  value: string 
  maxElements: string
  totalSupply: string
  paused: boolean
  phase1Winner: string
}

export default function Purchase(props: React.PropsWithChildren<Props>) {
  const { onSub, onAdd, onBuy, onInput, onInputValue, onFocusValue, qty, value, maxElements, totalSupply, paused, phase1Winner } = props;
  const rest = (isNaN(+maxElements) || isNaN(+totalSupply)) ? 0 : (Number(maxElements) - Number(totalSupply));
  const total = isNaN(+maxElements) ? 0 : +maxElements;
  return (
    <div className="Purchase">
      <div className="Purchase__header">
        <span>{rest}</span>
        <span> / </span>
        <span>{total}</span>
      </div>
      <div className="Purchase__body">
        <Container className="Purchase__row">
          <Row>
            <Col>
              <div className="Purchase__label">AMOUNTS</div>
              <div>
                <ETH />
                <span className="Purchase__num">{formatEther(value, 4)}</span>
                {/* <AutosizeInput
                  className="Purchase__input"
                  name="value"
                  value={formatEther(value, 4)}
                  onChange={onInputValue}
                  onFocus={onFocusValue}
                  inputStyle={{
                    fontSize: 64,
                    color: '#2F75EC',
                    border: 0,
                    background: 'transparent',
                    lineHeight: '74px',
                    fontWeight: 'bold',
                  }}
                /> */}
              </div>
            </Col>
            <Col>
              <div className="Purchase__label">QUANTITY</div>
              <div>
                <AddSub type="sub" onClick={onSub} />
                <AutosizeInput
                  className="Purchase__input"
                  name="qty"
                  value={qty}
                  onChange={onInput}
                  inputStyle={{
                    fontSize: 64,
                    color: '#2F75EC',
                    border: 0,
                    background: 'transparent',
                    lineHeight: '74px',
                    fontWeight: 'bold',
                  }}
                />
                <AddSub onClick={onAdd} />
              </div>
            </Col>
          </Row>
        </Container>
        <Container>
          <div className="Purchase__buy">
            <Button disabled={!totalSupply || (paused === true)} onClick={onBuy}>BUY</Button>
          </div>
        </Container>
      </div>
      <Container className="Purchase__winner">
        <label className="fw-bold">Winner</label>{formatAddress(phase1Winner)}
      </Container>
    </div>
  );
}