import Web3 from "web3";

export function formatEther (value, fixed) {
  if (!value) return "0.00";
  const s = Web3.utils.fromWei(String(value), "ether");
  return parseFloat(s).toFixed(fixed || 2);
}

export function formatAddress (value) {
  if (!value) return "0x";
  return value.slice(0, 5) + "..." + value.slice(-4);
}