import React from 'react';
import './style.css';
import ETH from '../ETH';
import Chest from '../Chest';
import { formatEther } from '../utils';

interface Props {
  amount: number
  info: string
  chest: boolean
  cup: boolean
  max: string
}

export default function Reward(props: React.PropsWithChildren<Props>) {
  return (
    <div className="Reward">
      <div className="Reward__chest">
        {props.chest && <Chest />}
      </div>
      <div>
        {props.cup && <RewardCup />}
      </div>
      <div>
        <div className="RewardAmount">
          <ETH />
          <span className="RewardNum">
            {formatEther(props.amount)} <label className="RewardNum__max"> / {props.max}</label> <label className="RewardNum__maxsuffix">MAX</label>
          </span>
        </div>
        <div className="RewardInfo">{props.info}</div>
      </div>
    </div>
  );
}

