import React, { useEffect, useState } from 'react';
import classNames from 'classnames';
import './style.css';

const countdown = global.countdown;
function getTime(cd) {
  if (!cd) {
    return {
      days: 0,
      hours: 0,
      minutes: 0,
      seconds: 0,
    };
  }

  return {
    days: cd.days,
    hours: cd.hours,
    minutes: cd.minutes,
    seconds: cd.seconds,
  };
}

function countdownItem(value, label) {
  return (
    <div className="CountdownItem">
      <div className="CountdownItem__num">{value}</div>
      <div className="CountdownItem__label">{label}</div>
    </div>
  );
}

let seconds;

export default function Countdown(props) {
  const { notstart, phase2EndBlockNumber, blockNumber } = props;
  const [time, setTime] = useState(getTime);

  useEffect(() => {
    if (isNaN(phase2EndBlockNumber) || isNaN(blockNumber)) return;
    if (!isNaN(seconds)) return;
    seconds = parseInt(((+phase2EndBlockNumber) - (+blockNumber)) / 13)
    if (seconds === 0) return;
    const end = Date.now() + (seconds * 1000)
    countdown(
      new Date(new Date(end)),
      function(ts) {
        setTime(getTime(ts))
      },
      countdown.DAYS|countdown.HOURS|countdown.MINUTES|countdown.SECONDS
    );
  }, [phase2EndBlockNumber, blockNumber]);
  
  const className = classNames('Countdown', {
    'Countdown--notstart': notstart,
  })

  return (
    <div className={className}>
      {notstart ?
        <div className="Countdown__time">
          {countdownItem('--', "DAYS")}
          {countdownItem('--', "HOURS")}
          {countdownItem('--', "MINUTES")}
          {countdownItem('--', "SECONDS")}
        </div> :
        <div className="Countdown__time">
        {countdownItem(time.days, "DAYS")}
        {countdownItem(time.hours, "HOURS")}
        {countdownItem(time.minutes, "MINUTES")}
        {countdownItem(time.seconds, "SECONDS")}
        </div>
      }
      <div className="Countdown__label">
        REMAINING TIME
      </div>
      {notstart &&
        <div className="Countdown__notstart">
          Not Start
        </div>
      }
    </div>
  );
}